<template>
  <transition-group appear appear-active-class="animated fadeIn">
    <h1 class="title" key="app-title">Memory Cards</h1>
    <div id="nav" key="app-nav">
      <router-link to="/">Игра</router-link> |
      <router-link to="/collection">Коллекция</router-link> |
      <router-link to="/records">Рекорды</router-link>
    </div>
  </transition-group>
  <div class="container">
    <router-view v-slot="{ Component }">
      <transition :enter-active-class="enterTransition"
                  :leave-active-class="leaveTransition">
        <component :is="Component" :key="$route.path" />
      </transition>
    </router-view>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex';

  export default {
    created() {
      this.getAnimals();
      this.$router.beforeEach((to, from, next) => {
        if (from.name == 'Table') {
          this.enterTransition = 'animated animateDelay bounceInRight';
          this.leaveTransition = 'animated bounceOutLeft';
          next();
        } else if (from.name == 'Collection' && to.name == 'Table') {
          this.enterTransition = 'animated animateDelay bounceInLeft';
          this.leaveTransition = 'animated bounceOutRight';
          next();
        } else if (from.name == 'Collection' || from.name == 'Table' && to.name == 'Records') {
          this.enterTransition = 'animated animateDelay bounceInRight';
          this.leaveTransition = 'animated bounceOutLeft';
          next();
        } else if (from.name == 'Records') {
          this.enterTransition = 'animated animateDelay bounceInLeft';
          this.leaveTransition = 'animated bounceOutRight';
          next();
        } else {
          next();
        }
      });
    },
    data: () => ({
      enterTransition: 'animated fadeIn',
      leaveTransition: ''
    }),
    computed: {
      ...mapGetters({ animals: 'getAllAnimals' })
    },
    methods: {
      ...mapActions(['getAnimals'])
    }
  }
</script>

