<template>
  <div class="collection">
    <!-- <h2 class="collection__title">This is your collection</h2> -->
    <button type="button" key="app-chests" class="chests__toggle" @click="showChests = true">
      <icon name="key"></icon>
      <span class="chests__count">{{ chestsLength }}</span>
    </button>
    <div class="collection__wrapper">
      <div v-for="(item, index) in animals" 
            :key="item.name+index"
            class="collection__item"
            :class="!item.unlocked ? 'locked' : ''"
      >
        <div class="collection__jar">
          <p class="collection__name">{{ item.name }}</p>
          <icon :name="item.image" :class="'collection__icon'"></icon>
        </div>
      </div>
    </div>
    <vue-final-modal v-model="showChests" 
                      classes="modal__container"
                      content-class="modal__content"
                      focus-trap
    >
      <button class="modal__close" type="button" @click="showChests = false">x</button>
      <h3 class="modal__title">Ящики</h3>
      <div class="modal__wrapper">
        <p class="modal__text">{{ chestText }}</p>
        <div class="chests__wrapper">
          <transition-group :leave-active-class="'animated rotateOutUpLeft'">
            <chest v-for="(chest, index) in chests" 
                  :key="chest.category+index"
                  :category="chest.category" 
                  :id="chest.id"
                  class="chests__item"
                  @click.once="openChest(chest)"
            ></chest>
          </transition-group>
        </div>
      </div>
    </vue-final-modal>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from "vuex";
  import Chest from '@/components/Chest';

  export default {
    components: {
      Chest
    },
    created() {
      this.getChests();
    },
    data: () => ({
      showChests: false,
      opening: false
    }),
    computed: {
      ...mapGetters({ animals: 'getAllAnimals', unlocked: 'getCntUnlocked', chests: 'getAllChests' }),
      chestsLength() {
        return this.chests ? Object.keys(this.chests).length : 0;
      },
      chestText() {
        let text = this.chestsLength == 1 ? 'У тебя есть 1 ящик:' :
                   this.chestsLength > 1 ? 'У тебя есть ' + this.chestsLength + ' ящика:' :
                   'Здесь ничего нет'

        return text;
      }
    },
    methods: {
      ...mapActions(['getChests', 'openChest'])
    },
    watch: {
      unlocked(newCnt) {
        if (newCnt == 15) {
          setTimeout(() => {
            alert('Теперь тебе доступен сложный уровень в игре!');
          }, 1000);
        }
      }
    }
  }
</script>

