<template>
  <div class="pairs">
    <transition :enter-active-class="'animated bounceIn'"
                :leave-active-class="'animated bounceOut'">
      <div class="table" :style="tableStyles" ref="table" v-if="gameStarted">
        <vue-flip v-for="(item, index) in deck" 
                  :key="item.name+index"
                  width="100%"
                  height="100%"
                  class="card"
                  v-model="item.flipped"
                  @click="flipCard(item)"
        >
          <template v-slot:front>
            <icon v-if="item.found" name="found"></icon>
            <icon v-else name="question"></icon>
          </template>
          <template v-slot:back>
            <icon :name="item.image"></icon>
          </template>
        </vue-flip>
      </div>
    </transition>
    <vue-final-modal v-model="showModal" 
                      classes="modal__container"
                      content-class="modal__content"
                      focus-trap
                      @click-outside="closeModal"
    >
      <h3 class="modal__title">Отлично!</h3>
      <div class="modal__wrapper">
        <div v-if="counter < 3">Выиграй {{ gamesToChest }} и получишь ящик.</div>
        <div v-else><b>Ты выиграл ящик</b><br>(можешь открыть его в коллекции)</div>
        <p><small>Счётчик переворотов: {{ flipCounter }}</small></p>
      </div>
      <div class="modal__action">
        <button class="modal__button" type="button" @click="closeModal">Играть ещё</button> 
        <button class="modal__button" type="button" @click="goToCollection">В коллекцию</button>
      </div>
    </vue-final-modal>
    <div style="display: inline-block; margin-right: 6px">Счётчик: {{ flipCounter }}</div>
    <div class="table__modes" v-if="unlocked >= 15">
      <button v-if="difficulty == 3" type="button" class="table__mode" @click="changeMode(2)">Переключить на лёгкий</button>
      <button v-else type="button" class="table__mode" @click="changeMode(3)">Переключить на сложный</button>
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapMutations, mapActions } from 'vuex';
  import VueFlip from 'vue-flip';

  export default {
    name: 'Table',
    components: {
      VueFlip
    },
    data: () => ({
      cardSelected: [],
      gameStarted: true,
      flipCounter: 0,
      showModal: false
    }),
    computed: {
      ...mapGetters({ deck: 'getDeck', unlocked: 'getCntUnlocked', counter: 'getChestCounter', difficulty: 'getDifficult' }),
      gameStatus() {
        return this.deck.every((item) => item.found == true);
      },
      gamesToChest() {
        let cnt = 3 - this.counter;
        let text = (cnt == 1) ? `ещё ${cnt} игру` : `ещё ${cnt} игры`;

        return text;
      },
      tableStyles() {
        return this.difficulty == 2 ? 'grid-template-columns: repeat(5, 1fr)' : 'grid-template-columns: repeat(6, 1fr)'
      }
    },
    methods: {
      ...mapMutations(['generateDeck']),
      ...mapActions(['addChest', 'getCounter', 'patchCounter', 'setDifficulty']),
      flipCard: function (card) {
        // If card not flipped or founded
        if (!card.found && !card.flipped) {
          // If selected less than 2 cards
          if (this.cardSelected.length < 2) {
            // Flip card
            card.flipped = true;
            this.cardSelected.push(card);
            this.flipCounter++;
            // If cards are same
            if (this.cardSelected.length === 2) {
              // Show check mark with timeout
              setTimeout(() => {
                this.cardSelected.map(el => el.flipped = false)
                if (this.cardSelected[0].name === card.name) {
                  this.cardSelected[0].found = true;
                  card.found = true;
                }
                this.cardSelected = []
              }, 800)
            }
          }
        }
      },
      restartGame: function () {
        this.gameStarted = false;

        setTimeout(() => {
          this.generateDeck();
          this.flipCounter = 0;
          this.gameStarted = true;
        }, 750);
      },
      closeModal: function () {
        this.showModal = false;
        this.restartGame();

        if (this.counter >= 3) {
          this.patchCounter(0);
        }
      },
      goToCollection: function () {
        this.showModal = false;

        if (this.counter >= 3) {
          this.patchCounter(0);
        }

        setTimeout(() => {
          this.$router.push({ name: 'Collection' });
        }, 200);
      },
      changeMode: function (value) {
        this.setDifficulty(value);
        this.restartGame();
      }
    },
    watch: {
      gameStatus(newStatus, oldStatus) {
        if (newStatus == true) {
          this.patchCounter(this.counter + 1);

          setTimeout(() => {
            this.showModal = true;
          }, 150);
        }
      },
      counter(newCnt, oldCnt) {
        if (newCnt >= 3) {
          this.addChest();
        }
      }
    },
    created() {
      this.$router.beforeEach((to, from, next) => {
        if (from.name == 'Table') {
          next();
          setTimeout(() => {
            this.restartGame();
          }, 500);
        } else {
          next();
        }
      });
    },
    mounted() {
      this.getCounter();
    }
  }
</script>

<style lang="less">
  .pairs {
    &__button {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      margin: auto;
    }
  }
  .table {
    display: grid;
    gap: 1vw;
    height: calc(100vh - 200px);
    margin-bottom: 10px;

    &__modes {
      display: inline-flex;
    }
  }
  .card {
    margin: auto;
    cursor: pointer;
    user-select: none;
    overflow: hidden;
  }
  .back, .front {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    border-radius: 8px;
  }
  .front {
    padding: 0;
    color: #fff;

    img, svg {
      width: 98%;
      max-width: 150px;
      max-height: 100%;
    }
  }
  .back {
    padding: 0.75vw 1.5vw;
    
    img, svg {
      width: 95%;
      max-width: 130px;
      max-height: 100%;
    }
  }
</style>
