import { createStore } from 'vuex';
import axios from 'axios';
import firebase from 'firebase/app';

// const _source = './images/animals/';
// const _sourceicons = './images/icons/';
// const _database = axios.create({
//   baseURL: 'http://localhost:3000/'
// });

export default createStore({
  state: {
    animals: [],
    deck: [],
    difficulty: 2,
    chests: [],
    chestCounter: 0,
    opening: {}
  },
  getters: {
    getAllAnimals(state) {
      return state.animals;
    },
    getDeck(state) {
      return state.deck;
    },
    getCntUnlocked(state) {
      let unlocked = state.animals.filter((item) => item.unlocked == true);

      return unlocked.length;
    },
    getRandomLocked(state) {
      let animalLocked = state.animals.filter((item) => item.unlocked == false && 
                                                        item.category == state.opening.category);
      let indexRandom = Math.floor(Math.random()*animalLocked.length);
      let animal = animalLocked[indexRandom];

      return { category: animal.category, id: animal.id, wasIn: state.opening.id };
    },
    getAllChests(state) {
      return state.chests;
    },
    getChestCounter(state) {
      return state.chestCounter;
    },
    getDifficult(state) {
      return state.difficulty;
    }
  },
  mutations: {
    addAnimals: function (state, animals) {
      state.animals = animals;
    },
    addChests: function (state, chests) {
      state.chests = chests;
    },
    updateAnimalUnlocked: function (state, update) {
      let animal = state.animals[update.id];

      animal.name = update.name;
      animal.image = update.image; // _source+update.image svg version
      animal.unlocked = update.unlocked;
    },
    generateDeck: function (state) {
      let animals = state.animals;
      let animalsUnlocked = animals.filter((item) => item.unlocked == true);
      let animalsDeck = getSortedCategories(animalsUnlocked, ["blue", "green", "orange", "purple", "brown"], state.difficulty);

      let deck = [...animalsDeck, ...animalsDeck]
      .map((card) => {
        return {
          ...card,
          flipped: false,
          found: false
        }
      }).sort(() => 0.5 - Math.random());

      state.deck = deck;
    },
    updateChests: function (state, id) {
      delete state.chests[id]
    },
    changeOpeningChest: function (state, chest) {
      state.opening = chest;
    },
    setCounter: function (state, value) {
      state.chestCounter = value;
    },
    setDifficulty: function (state, value) {
      state.difficulty = value;
    }
  },
  actions: {
    getAnimals: function (store) {
      firebase.database().ref('animals').once('value')
      .then((response) => {
        let animals = setSource(response.val());
        
        store.dispatch('getDifficulty');
        store.commit('addAnimals', animals);
      })
      .catch((error) => console.log(error+' in getAnimals action'));
    },
    unlockAnimal: function (store, animal) {
      firebase.database().ref('animals/'+animal.id).update({ unlocked: true }, (error) => {
        if (error) {
          console.log(error);
        } else {
          firebase.database().ref('animals/'+animal.id).once('value')
          .then((response) => {
            store.commit('updateAnimalUnlocked', response.val());
            store.dispatch('deleteChest', animal.wasIn);
            store.commit('generateDeck');
          })
          .catch((promiseError) => console.log(promiseError+' in unlockAnimal action'));
        }
      })
    },
    getChests: function (store) {
      firebase.database().ref('chests').once('value')
      .then((response) => {
        let chests = response.val();

        store.commit('addChests', chests);
      })
      .catch((error) => console.log(error+' in getchests action'));
    },
    openChest: function (store, chest) {
      store.commit('changeOpeningChest', chest);
      store.dispatch('unlockAnimal', store.getters.getRandomLocked);
    },
    addChest: function (store) {
      firebase.database().ref('nextchest/0').once('value')
      .then((response) => { 
        let chestCategory = response.val().category;
        let nextChestCategory = changeNextChestCategory(chestCategory);
        let newChestKey = firebase.database().ref('chests').push().key;

        firebase.database().ref('chests/'+newChestKey).set({ category: chestCategory, id: newChestKey });
        store.dispatch('getChests');
        firebase.database().ref('nextchest/0').update({ category: nextChestCategory });
      })
      .catch((error) => console.log(error+' in addChest action'));
    },
    deleteChest: function (store, id) {
      firebase.database().ref('chests/'+id).update({ category: null, id: null }, (error) => {
        if (error) {
          console.log(error);
        } else {
          store.commit('updateChests', id);
          store.commit('changeOpeningChest', {});
        }
      });
    },
    getCounter: function (store) {
      firebase.database().ref('counter').once('value')
      .then((response) => {
        let cnt = response.val().value;
        console.log(cnt);
        
        if(cnt >= 3) {
          store.dispatch('patchCounter', 0);
        }
        else {
          store.commit('setCounter', response.val().value);
        }
      })
      .catch((error) => console.log(error+' in getCounter action'));
    },
    patchCounter: function (store, cnt) {
      firebase.database().ref('counter').update({ value: cnt }, (error) => {
        if (error) {
          console.log(error);
        } else {
          firebase.database().ref('counter').once('value')
          .then((response) => {
            store.commit('setCounter', response.val().value);
          })
          .catch((pomiseError) => console.log(pomiseError+' in patchCounter action'));
        }
      })
    },
    getDifficulty: function (store) {
      firebase.database().ref('rules').once('value')
      .then((response) => {
        store.commit('setDifficulty', response.val().difficulty);
        store.commit('generateDeck');
      })
      .catch((error) => console.log(error+' in getDifficulty action'));
    },
    setDifficulty: function (store, pairs) {
      firebase.database().ref('rules').update({ difficulty: pairs }, (error) => {
        if (error) {
          console.log(error);
        } else {
          firebase.database().ref('rules').once('value')
          .then((response) => {
            store.commit('setDifficulty', response.val().difficulty);
          })
          .catch((error) => console.log(error+' in setDifficulty action'));
        }
      });
    }
  },
})

function setSource(animals) {
  return animals.map((item) => {
    if (item.unlocked == true) {
      return {
        name: item.name,
        image: item.image,
        unlocked: item.unlocked,
        category: item.category,
        id: item.id
      }
    }
    else {
      return {
        name: '???',
        image: 'question',
        unlocked: item.unlocked,
        category: item.category,
        id: item.id
      }
    }
  });
}

function getSortedCategories(animals, categories, pairs) {
  let result = [];

  categories.forEach(category => {
    let categorySorted = sortCategory(animals, category, pairs);
    result = result.concat(categorySorted);
  });

  return result;
}

function sortCategory(animals, category, pairs) {
  return animals.filter((item) => item.category == category).sort(() => 0.5 - Math.random()).slice(0, pairs);
}

function changeNextChestCategory(oldCategory) {
  let newCategory = oldCategory == "blue" ? "green" :
                    oldCategory == "green" ? "orange" :
                    oldCategory == "orange" ? "purple" :
                    oldCategory == "purple" ? "brown" :
                    "blue";

  return newCategory;
}